.projectsPageWrapper {
  height: 100vh;
  background:
  radial-gradient(circle at 5vw 0, #FFB9CD,
    #FFB9CD 35vw, transparent 35vw);
}

.projectsPageWrapper h1 {
  color: #008F82;
  font-size: calc(20px + 1vw + 0.3vh);
  font-weight: bold;
}

.projectText {
  color: white;
  font-size: calc(14px + 0.7vw + 0.2vh);
  width: calc(30px + 13vh + 3vw);
  text-align: center;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.circle {
  background: #F1F1F1;
  border-radius: 50%;
  height: calc(40px + 15vh + 6.5vw);
  width: calc(40px + 15vh + 6.5vw);
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  z-index: 1;
  transition: background 0.5s ease-in-out;
}

.circle:hover {
  background: rgba(64, 191, 180, 0.8);
}

.circle:hover .projectText{
  opacity: 1;
}

.circle .airbnb {
  position: absolute;
  height: calc(14px + 7vh + 14vw);
  opacity: 0.5;
  transition: 0.5s ease-in-out;
}

.circle:hover .airbnb {
  opacity: 0;
}


#beakspeak {
  margin-top: 12vh;
  margin-left: 9vw;
  height: 40vh;
  width: 40vh;
  position: absolute;
}

