  position: absolute;
  z-index: 2;
}

#website {
  margin-top: 55vh;
  margin-left: 20vw;
  height: 35vh;
  width: 35vh;
  text-align: right;
  position: absolute;
}

#wTitle {
  margin-top: calc(4px + 4vh + 7.5vw);
  margin-left: -5vw;
  width: 15vw;
  position: absolute;
  z-index: 2;
}

#hedwig {
  margin-top: 25vh;
  margin-left: 45vw;
  height: 35vh;
  width: 35vh;
  text-align: right;
  position: absolute;
}

#HwTitle {
  margin-top: calc(4px + 6vh + 7.5vw);
  margin-left: -5vw;
  width: 15vw;
